import * as React from 'react';
import InfoProps from '../props/InfoProps';

export default class Info extends React.Component<InfoProps> {
  constructor(props: InfoProps) {
    super(props);
  }

  render(): JSX.Element {
    return (
      <div className="info">
        <p> 3={this.props.book3} 4={this.props.book4} 5={this.props.book5}</p>
        <p> {this.props.medalCount}</p>
        <p> ={this.props.budCount} ={this.props.flowerCount} <span style={{ color: 'red' }}>...</span></p>
        <p> {this.props.scoutCost} <span style={{ color: 'red' }}>...</span></p>
        <p> {this.props.levelUpCost}</p>
      </div>
    );
  }
}